'use strict'

/**
 * Usage:
 *
 *  import session from './session';
 *
 *  _.get('/',
 *    session('authcheck')
 *  );
 */

import debug from 'debug';

const trace = debug('ow:session:trace');
const error = debug('ow:session:error');

let authcheck = (ctx) => {
  let user = ctx.session.user;

  if (user) {
    return true;
  }

  return false;
};

export default function(params) {
  return function *(next) {
    yield next;
  }
}